import { fetchCountries } from "./fetchCountries";
// import debounce from "lodash.debounce";
// import { Notify } from "pnotify";

const searchInputEl = document.querySelector("#search-country");
const countriesListEl = document.querySelector(".countries-list");
const countryInfoEl = document.querySelector(".country-info");

const renderCountriesList = (countries) => {
  countriesListEl.innerHTML = countries.map((country) => {
    `<li class="country-item">
        <p>${country.name}</p>
    </li>`;
  }).join("");

  countryInfoEl.innerHTML = "";
};

const renderCountryInfo = (country) => {
    const { name, capital, population, language, flags } = country;

    countryInfoEl.innerHTML = `
    <h2 class="country-title">${name}</h2>
    <div class="country-couple>
        <ul class="country-list">
            <p class="country-capital">Capital: ${capital}</p>
            <p class="country-population">Population: ${population}</p>
            <p class="country-languages>Languages:</p>
            <ul class="languages-list">
                ${language}
            </ul>
        </ul>
        <img src="${flags}" alt="Flag of ${name}" class="country-flag"/>
    </div>
    `;

    countriesListEl.innerHTML = "";
};

searchInputEl.addEventListener("input", debounce((e) => {
  const query = e.target.value.trim();

  fetchCountries(query).then((countries) => {
    if (countries.length === 1) {
        renderCountryInfo(countries[0]);
    } else {
        renderCountriesList(countries);
    }
    //  else if (countries.length <= 10) {
    //   console.log(2);
    // } else {
    //   console.log("error");
    // }
  });
}, 500));

fetchCountries(searchInputEl);
